.media-object {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.media-left {
  flex: 1 1 30%;
}

.media-right {
  flex: 1 1 70%;
  margin-left: 32px;
  text-align: left;
}

@media (max-width: 575px) {
  .media-object {
    display: block;
  }

  .media-left {
    float: left;
    margin: 0 1.5rem 0 0;

    img {
      max-width: 100px;
    }
  }

  .media-right {
    margin: 0;
  }
}
