/*************************************
Main layout styling
*************************************/

* {
  box-sizing: border-box;

  &::after,
  &::before {
    box-sizing: inherit;
  }
}

body {
  padding: 4rem 2rem;
  background-color: $dark-blue;
}

.page-wrapper {
  margin: 0 auto;
  max-width: 80rem;
}

.panel {
  padding: 2rem;

  &.panel-light {
    background-color: $panel-light-background-color;
    border-top: 0.8rem solid $panel-light-border-color;
    color: $panel-light-color;
  }
}

@media (max-width: 767px) {
  body {
    padding: 0;
    margin: 0;
  }
  .page-wrapper {
    padding: 2rem;
  }
}

@media (max-width: 575px) {
  .panel {
    padding: 0;
  }
}
